/* -------------------------------------------------------------------------- */
/*                                CSS Variables                               */
/* -------------------------------------------------------------------------- */
:root {
  /* --------------------------------- Colors --------------------------------- */
  --nfd-editor-chat-color-primary: #2271b1;
  --nfd-editor-chat-color-primary-hover: #135e96;
  --nfd-editor-chat-color-primary-active: #0e4b72;
  --nfd-editor-chat-color-primary-light-8: rgba(34, 113, 177, 0.08);
  --nfd-editor-chat-color-primary-light-12: rgba(34, 113, 177, 0.12);
  --nfd-editor-chat-color-primary-light-16: rgba(34, 113, 177, 0.16);
  --nfd-editor-chat-color-primary-light-20: rgba(34, 113, 177, 0.2);
  --nfd-editor-chat-color-grey-dark: #666;
  --nfd-editor-chat-color-grey-medium: #999;
  --nfd-editor-chat-color-grey-light: #ddd;
  --nfd-editor-chat-color-grey-placeholder: #9e9e9e;
  --nfd-editor-chat-color-border: #e0e0e0;
  --nfd-editor-chat-color-background: #f5f5f5;
  --nfd-editor-chat-color-text: #1e1e1e;
  --nfd-editor-chat-color-text-secondary: #424242;
  --nfd-editor-chat-color-white: #fff;
  /* -------------------------------- Typography -------------------------------- */
  --nfd-editor-chat-font-size-base: 14px;
  --nfd-editor-chat-font-size-xl: 22px;
  --nfd-editor-chat-font-size-lg: 20px;
  --nfd-editor-chat-font-size-md: 16px;
  --nfd-editor-chat-font-size-sm: 12px;
}

.components-button[aria-controls*=nfd-editor-chat].is-active, .components-button[aria-controls*=nfd-editor-chat]:hover, .components-button[aria-controls*=nfd-editor-chat]:focus, .components-button[aria-controls*=nfd-editor-chat].is-pressed {
  background: transparent !important;
  color: inherit !important;
}

.nfd-editor-chat-ai-avatar {
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.nfd-editor-chat-ai-avatar svg {
  fill: currentColor;
}

.nfd-editor-chat-suggestions {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  width: 100%;
  padding: 0 0 16px 0;
  justify-content: center;
}

.nfd-editor-chat-suggestion {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: var(--nfd-editor-chat-color-white);
  border: 1px solid var(--nfd-editor-chat-color-border);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.15s ease;
  text-align: left;
  white-space: nowrap;
  flex-shrink: 0;
  color: var(--nfd-editor-chat-color-grey-dark);
}
.nfd-editor-chat-suggestion.components-button svg {
  fill: none;
}
.nfd-editor-chat-suggestion.components-button:hover {
  background: var(--nfd-editor-chat-color-background);
  color: var(--nfd-editor-chat-color-text);
}
.nfd-editor-chat-suggestion.components-button:active {
  background: var(--nfd-editor-chat-color-grey-light);
  color: var(--nfd-editor-chat-color-text);
}
.nfd-editor-chat-suggestion.components-button:focus {
  outline: none;
  background: var(--nfd-editor-chat-color-background);
  border-color: var(--nfd-editor-chat-color-primary);
  color: var(--nfd-editor-chat-color-text);
}

.nfd-editor-chat-suggestion__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  stroke: var(--nfd-editor-chat-color-grey-dark);
}
.nfd-editor-chat-suggestion__icon svg {
  width: 14px;
  height: 14px;
  fill: none;
}

.nfd-editor-chat-suggestion__text {
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.3;
  font-weight: 400;
  text-align: left;
}

.nfd-editor-chat-error-alert {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  padding: 12px;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 12px;
  color: #dc2626;
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.5;
}

.nfd-editor-chat-error-alert__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  color: #dc2626;
  stroke: currentColor;
  fill: none;
}

.nfd-editor-chat-error-alert__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.interface-complementary-area__fill:has(#nfd-editor-chat),
#nfd-editor-chat {
  width: 400px !important;
}

#nfd-editor-chat {
  display: flex;
  flex-direction: column;
  max-height: 100%;
  overflow: hidden;
}

.nfd-editor-chat-sidebar__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--nfd-editor-chat-color-white);
  padding: 0;
  position: relative;
  flex-grow: 1;
}

.nfd-editor-chat-sidebar__panel {
  overflow-y: auto;
}

.interface-complementary-area.nfd-editor-chat-sidebar {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.interface-complementary-area .nfd-editor-chat-sidebar__header {
  position: relative;
}

.components-panel.nfd-editor-chat-sidebar__panel {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.nfd-editor-chat-sidebar__header {
  padding: 16px;
  border-bottom: 1px solid #e0e0e0;
  position: relative;
}
.nfd-editor-chat-sidebar__header h2 {
  margin: 0;
  font-size: var(--nfd-editor-chat-font-size-base);
  font-weight: 600;
}

.nfd-editor-chat-sidebar__header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nfd-editor-chat-sidebar__header-actions {
  display: flex;
  gap: 4px;
  align-items: center;
}

.nfd-editor-chat-sidebar__new-chat.components-button,
.nfd-editor-chat-sidebar__expand.components-button {
  min-width: auto;
  width: 32px;
  height: 32px;
  padding: 0;
  stroke: var(--nfd-editor-chat-color-grey-dark);
  background: transparent;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.nfd-editor-chat-sidebar__new-chat.components-button:hover:not(:disabled),
.nfd-editor-chat-sidebar__expand.components-button:hover:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-12);
}
.nfd-editor-chat-sidebar__new-chat.components-button:focus:not(:disabled),
.nfd-editor-chat-sidebar__expand.components-button:focus:not(:disabled) {
  outline: none;
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-16);
}
.nfd-editor-chat-sidebar__new-chat.components-button:active:not(:disabled),
.nfd-editor-chat-sidebar__expand.components-button:active:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary-hover);
  background: var(--nfd-editor-chat-color-primary-light-20);
}
.nfd-editor-chat-sidebar__new-chat.components-button svg,
.nfd-editor-chat-sidebar__expand.components-button svg {
  display: block;
  fill: none;
  width: 16px;
  height: 16px;
}

.nfd-editor-chat-input {
  padding: 16px;
  background: var(--nfd-editor-chat-color-white);
  z-index: 10;
  border-top: 1px solid var(--nfd-editor-chat-color-border);
}

.nfd-editor-chat-input__disclaimer {
  text-align: center;
  font-size: 12px;
  color: var(--nfd-editor-chat-color-grey-medium);
  margin-top: 8px;
  line-height: 1.4;
}
.nfd-editor-chat-input__disclaimer a {
  color: var(--nfd-editor-chat-color-grey-dark);
  text-decoration: none;
  font-weight: 500;
}
.nfd-editor-chat-input__disclaimer a:hover {
  text-decoration: underline;
}

.nfd-editor-chat-input__container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: var(--nfd-editor-chat-color-white);
  border-radius: 12px;
  padding: 12px;
  border: 1px solid var(--nfd-editor-chat-color-border);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
}
.nfd-editor-chat-input__container:focus-within {
  box-shadow: 0 2px 12px rgba(34, 113, 177, 0.25);
}

.nfd-editor-chat-input__textarea {
  width: 100%;
  border: none;
  background: transparent;
  resize: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.5;
  padding: 4px;
  color: var(--nfd-editor-chat-color-text);
  min-height: 24px;
  max-height: 200px;
  overflow-y: auto;
}
.nfd-editor-chat-input__textarea:focus {
  outline: none;
  border: none;
  box-shadow: none;
}
.nfd-editor-chat-input__textarea::placeholder {
  color: var(--nfd-editor-chat-color-grey-placeholder);
}
.nfd-editor-chat-input__textarea:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  box-shadow: none;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar {
  width: 8px;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar-track {
  background: transparent;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar-thumb {
  background: var(--nfd-editor-chat-color-border);
  opacity: 0.8;
  border-radius: 4px;
}
.nfd-editor-chat-input__textarea::-webkit-scrollbar-thumb:hover {
  opacity: 1;
}

.nfd-editor-chat-input__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
}

.components-button.nfd-editor-chat-input__submit {
  flex-shrink: 0;
  min-width: auto;
  width: 28px;
  height: 28px;
  padding: 0;
  stroke: var(--nfd-editor-chat-color-grey-dark);
  background: var(--nfd-editor-chat-color-background);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.components-button.nfd-editor-chat-input__submit:hover:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-12);
}
.components-button.nfd-editor-chat-input__submit:focus:not(:disabled) {
  outline: none;
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-16);
}
.components-button.nfd-editor-chat-input__submit:active:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary-hover);
  background: var(--nfd-editor-chat-color-primary-light-20);
}
.components-button.nfd-editor-chat-input__submit:disabled {
  opacity: 0.4;
  stroke: var(--nfd-editor-chat-color-grey-medium);
  background: var(--nfd-editor-chat-color-background);
  cursor: not-allowed;
}
.components-button.nfd-editor-chat-input__submit svg {
  display: block;
  fill: none;
  width: 16px;
  height: 16px;
}

.components-button.nfd-editor-chat-input__attach {
  flex-shrink: 0;
  min-width: auto;
  width: 28px;
  height: 28px;
  padding: 0;
  stroke: var(--nfd-editor-chat-color-text);
  background: var(--nfd-editor-chat-color-background);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s ease;
}
.components-button.nfd-editor-chat-input__attach:hover:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-12);
}
.components-button.nfd-editor-chat-input__attach:focus:not(:disabled) {
  outline: none;
  stroke: var(--nfd-editor-chat-color-primary);
  background: var(--nfd-editor-chat-color-primary-light-16);
}
.components-button.nfd-editor-chat-input__attach:active:not(:disabled) {
  stroke: var(--nfd-editor-chat-color-primary-hover);
  background: var(--nfd-editor-chat-color-primary-light-20);
}
.components-button.nfd-editor-chat-input__attach:disabled {
  opacity: 0.4;
  stroke: var(--nfd-editor-chat-color-grey-medium);
  background: var(--nfd-editor-chat-color-background);
  cursor: not-allowed;
}
.components-button.nfd-editor-chat-input__attach svg {
  display: block;
  fill: none;
  width: 16px;
  height: 16px;
}

.nfd-editor-chat-messages {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  overflow-y: auto;
  padding: 20px 24px;
  gap: 20px;
  min-height: 0;
}

.nfd-editor-chat-message {
  display: flex;
  gap: 12px;
  align-items: flex-start;
  font-size: var(--nfd-editor-chat-font-size-base);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content {
  width: 100%;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content > * {
  margin-bottom: 12px;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h1,
.nfd-editor-chat-message .nfd-editor-chat-message__content h2,
.nfd-editor-chat-message .nfd-editor-chat-message__content h3,
.nfd-editor-chat-message .nfd-editor-chat-message__content h4,
.nfd-editor-chat-message .nfd-editor-chat-message__content h5,
.nfd-editor-chat-message .nfd-editor-chat-message__content h6 {
  line-height: 1.2;
  font-weight: 600;
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  color: var(--nfd-editor-chat-color-text);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h1 {
  font-size: var(--nfd-editor-chat-font-size-xl);
  margin-top: 0;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h2 {
  font-size: var(--nfd-editor-chat-font-size-lg);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h3 {
  font-size: var(--nfd-editor-chat-font-size-md);
  text-transform: initial;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h4 {
  font-size: var(--nfd-editor-chat-font-size-md);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h5 {
  font-size: var(--nfd-editor-chat-font-size-sm);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content h6 {
  font-size: var(--nfd-editor-chat-font-size-sm);
  font-weight: 500;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content p {
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.6;
  margin-bottom: 1rem;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content strong {
  font-weight: 600;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content em {
  font-style: italic;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content u {
  text-decoration: underline;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content s {
  text-decoration: line-through;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content mark {
  background-color: var(--nfd-editor-chat-color-primary-light-20);
  padding: 0.1em 0.2em;
  border-radius: 2px;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content small {
  font-size: 0.875em;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content sub,
.nfd-editor-chat-message .nfd-editor-chat-message__content sup {
  font-size: 0.75em;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content sup {
  top: -0.5em;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content sub {
  bottom: -0.25em;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content a {
  color: var(--nfd-editor-chat-color-primary);
  text-decoration: none;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content a:hover {
  text-decoration: underline;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content code {
  background-color: var(--nfd-editor-chat-color-border);
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-family: "Courier New", Courier, monospace;
  font-size: 0.9em;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content ul,
.nfd-editor-chat-message .nfd-editor-chat-message__content ol {
  margin: 1rem 0;
  padding-left: 1.5rem;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content ul {
  list-style-type: disc;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content ol {
  list-style-type: decimal;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content li {
  margin-bottom: 0.5rem;
  line-height: 1.5;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content ul ul,
.nfd-editor-chat-message .nfd-editor-chat-message__content ol ol,
.nfd-editor-chat-message .nfd-editor-chat-message__content ul ol,
.nfd-editor-chat-message .nfd-editor-chat-message__content ol ul {
  margin: 0.5rem 0;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content ul ul {
  list-style-type: circle;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content ul ul ul {
  list-style-type: square;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content dl {
  margin: 1rem 0;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content dt {
  font-weight: 600;
  margin-top: 1rem;
  margin-bottom: 0.25rem;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content dd {
  margin-left: 1.5rem;
  margin-bottom: 0.5rem;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content blockquote {
  margin: 1.5rem 0;
  padding: 1rem 1.5rem;
  background-color: var(--nfd-editor-chat-color-background);
  border-left: 4px solid var(--nfd-editor-chat-color-primary);
  font-style: italic;
  line-height: 1.6;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content pre {
  background-color: var(--nfd-editor-chat-color-background);
  padding: 1rem;
  border-radius: 4px;
  overflow-x: auto;
  margin: 1rem 0;
  font-family: "Courier New", Courier, monospace;
  font-size: 0.9em;
  line-height: 1.4;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content pre code {
  background: none;
  padding: 0;
  border-radius: 0;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content table {
  width: 100%;
  border-collapse: collapse;
  margin: 1rem 0;
  font-size: 0.9em;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content th,
.nfd-editor-chat-message .nfd-editor-chat-message__content td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid var(--nfd-editor-chat-color-grey-light);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content th {
  background-color: var(--nfd-editor-chat-color-background);
  font-weight: 600;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content hr {
  border: none;
  border-top: 1px solid var(--nfd-editor-chat-color-grey-light);
  margin: 2rem 0;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content .note {
  background-color: var(--nfd-editor-chat-color-background);
  border: 1px solid var(--nfd-editor-chat-color-border);
  border-left: 4px solid var(--nfd-editor-chat-color-primary);
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 4px;
  line-height: 1.6;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content address {
  font-style: italic;
  margin: 1rem 0;
  line-height: 1.6;
}
.nfd-editor-chat-message .nfd-editor-chat-message__content time {
  color: var(--nfd-editor-chat-color-grey-dark);
}
.nfd-editor-chat-message .nfd-editor-chat-message__content * {
  unicode-bidi: normal;
}
.nfd-editor-chat-message--assistant {
  flex-direction: row;
  justify-content: flex-start;
}
.nfd-editor-chat-message--assistant .nfd-editor-chat-message__content {
  flex: 1;
  line-height: 1.6;
  color: var(--nfd-editor-chat-color-text);
  padding-top: 4px;
  background: none;
  padding: 0;
  border-radius: 0;
  white-space: pre-wrap;
}
.nfd-editor-chat-message--user {
  justify-content: flex-end;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__avatar {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: var(--nfd-editor-chat-color-border);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--nfd-editor-chat-color-text-secondary);
  font-weight: 600;
  overflow: hidden;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__avatar .nfd-editor-chat-message__avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__content {
  background: var(--nfd-editor-chat-color-background);
  padding: 12px 16px;
  border-radius: 12px;
  line-height: 1.5;
  color: var(--nfd-editor-chat-color-text-secondary);
  max-width: 70%;
  white-space: pre-wrap;
  position: relative;
}
.nfd-editor-chat-message--user .nfd-editor-chat-message__content::after {
  content: "";
  position: absolute;
  top: 10px;
  right: -6px;
  width: 0;
  height: 0;
  border-left: 6px solid var(--nfd-editor-chat-color-background);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}

.nfd-editor-chat-messages::-webkit-scrollbar {
  width: 8px;
}
.nfd-editor-chat-messages::-webkit-scrollbar-track {
  background: transparent;
}
.nfd-editor-chat-messages::-webkit-scrollbar-thumb {
  background: var(--nfd-editor-chat-color-border);
  opacity: 0.8;
  border-radius: 4px;
}
.nfd-editor-chat-messages::-webkit-scrollbar-thumb:hover {
  opacity: 1;
}

.nfd-editor-chat-welcome {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 40px 16px 0 16px;
  text-align: center;
  min-height: 0;
  overflow-y: auto;
  gap: 32px;
}

.nfd-editor-chat-welcome__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.nfd-editor-chat-welcome__avatar {
  margin-bottom: 24px;
}

.nfd-editor-chat-welcome__message {
  max-width: 320px;
  margin-bottom: 0;
}

.nfd-editor-chat-welcome__title {
  font-size: 18px;
  line-height: 1.4;
  color: var(--nfd-editor-chat-color-text);
  font-weight: 600;
  margin-bottom: 8px;
}

.nfd-editor-chat-welcome__subtitle {
  font-size: var(--nfd-editor-chat-font-size-base);
  line-height: 1.5;
  color: var(--nfd-editor-chat-color-grey-dark);
  font-weight: 400;
}

.nfd-editor-chat-typing-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  padding-top: 4px;
}

.nfd-editor-chat-typing-indicator__message {
  font-size: var(--nfd-editor-chat-font-size-base);
  color: var(--nfd-editor-chat-color-grey-medium);
  font-style: italic;
}

.nfd-editor-chat-typing-indicator__loader {
  width: 20px;
  aspect-ratio: 2;
  --_g: no-repeat
  	radial-gradient(circle closest-side, var(--nfd-editor-chat-color-grey-medium) 90%, transparent);
  background: var(--_g) 0% 50%, var(--_g) 50% 50%, var(--_g) 100% 50%;
  background-size: 33.3333333333% 40%;
  animation: nfd-editor-chat-loader 1s infinite linear;
}

@keyframes nfd-editor-chat-loader {
  20% {
    background-position: 0% 100%, 50% 50%, 100% 50%;
  }
  40% {
    background-position: 0% 0%, 50% 100%, 100% 50%;
  }
  60% {
    background-position: 0% 50%, 50% 0%, 100% 100%;
  }
  80% {
    background-position: 0% 50%, 50% 50%, 100% 0%;
  }
}